<template>
  <div class="mdl-layout mdl-js-layout mdl-layout--fixed-header">
    <header class="mdl-layout__header">
      <div class="mdl-layout__header-row">
        <span class="mdl-layout-title">Testing PWA</span>
      </div>
    </header>
    <div class="mdl-layout__drawer">
      <span class="mdl-layout-title">Testing PWA</span>
      <nav class="mdl-navigation">
        <router-link class="mdl-navigation__link" to="{ name: 'home'}" @click.native="hideMenu">Home</router-link>
      </nav>
    </div>
    <main class="mdl-layout__content">
      <div class="page-content">
        <router-view></router-view>
      </div>
    </main>
    <simplert></simplert>
  </div>
</template>
<script>
require('material-design-lite')
export default {
  name: 'app',
  methods: {
    hideMenu: function () {
      document.getElementsByClassName('mdl-layout__drawer')[0].classList.remove('is-visible')
      document.getElementsByClassName('mdl-layout__obfuscator')[0].classList.remove('is-visible')
    }
  }
}
</script>
<style>
  @import url('https://fonts.googleapis.com/icon?family=Material+Icons');
  @import url('https://code.getmdl.io/1.2.1/material.blue-red.min.css');
  @import '../public/vendor/bootstrap/css/bootstrap.min.css';
  @import '../public/dist/css/sb-admin-2.css';
  @import '../public/vendor/font-awesome/css/font-awesome.min.css';
</style>



